.uxora-content{
    &-thumb{
      img{
        width: 100%;
      }
        &.thumb2{
            text-align: right;
            @include max (1399px){
              padding-left: 40px;
            }
            @include max (1199px){
              padding-left: 30px;
            }
            @include max (991px){
              text-align: left;
              padding-left: 0;
            }
        }
        @include max (991px){
            padding-bottom: 40px;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
    }
}

.uxora-tab-thumb {
  padding: 50px;
  background-color: var(--white-bg);
  border-radius: 10px;
}

.tabs{
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 24px;
  flex-wrap: wrap;
  padding-bottom: 50px;
  position: relative;
  padding-top: 30px;
  @include max (1399px){
    padding-top: 0;
  }
  @include max (991px){
    padding-bottom: 0;
  }
  &::before{
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    z-index: 0;
    height: 3px;
    background-color: var(--light-color);
    @include max (1399px){
      display: none;
    }
  }
  @include max (991px){
    margin-bottom: 40px;
  }
  @include max (767px){
    gap: 12px;
    margin-bottom: 35px;
  }
  li{
    font-family: $heading-font;
    font-size: 24px;
    font-weight: 600;
    line-height: 32px;
    text-align: center;
    cursor: pointer;
    &.current{
      color: var(--accent-color);
      position: relative;
      &::before{
        position: absolute;
        content: "";
        top: -30px;
        left: 0;
        z-index: 1;
        width: 100%;
        height: 3px;
        background-color: var(--accent-color);
        @include max (1399px){
          display: none;
        }
      }
    }
  }
}
.max-610{
  max-width: 610px;
}

.uxora-tab-content{
	display: none;

}

.uxora-tab-content.current{
	display: inherit;
}
.uxora-tab-content{
  padding: 50px;
  border-radius: 10px;
  background-color: var(--white-bg);
  @include max (767px){
    padding: 40px 20px;
  }
  h3{
    font-size: 40px!important;
  }
}
.pr-40{
  @include max (1399px){
    padding-right: 40px;
  }
  @include max (1199px){
    padding-right: 30px;
  }
  @include max (991px){
    padding-right: 0px;
  }
}