.uxora-section-padding{
    padding: 130px 0;
    @include max (991px){
        padding: 80px 0;
    }
    @include max (767px){
        padding: 60px 0;
    }
}
.uxora-section-padding2{
    padding: 130px 0 106px;
    @include max (991px){
        padding: 80px 0 56px;
    }
    @include max (767px){
        padding: 60px 0 36px;
    }
}
.uxora-section-padding3{
    padding: 0 0 106px;
    @include max (991px){
        padding: 0 0 76px;
    }
    @include max (767px){
        padding: 0 0 36px;
    }
}
.uxora-section-padding4{
    padding: 0 0 130px;
    @include max (991px){
        padding: 0 0 80px;
    }
    @include max (767px){
        padding: 0 0 60px;
    }
}
.uxora-section-padding5{
    padding: 195px 0 130px;
    @include max (991px){
        padding: 150px 100px;
    }
    @include max (767px){
        padding:130px 0 80px;
    }
}

.ml-30{
    margin-left: 30px;
    @include max(1199px) {
        margin-left: 0;
    }
}

.mr-50{
    margin-right: 50px;
    @include max(1199px) {
        margin-right: 0;
    }
}






// section title

.uxora-section-title{
    padding-bottom: 80px;
    @include max (991px){
        padding-bottom: 60px;
        text-align: center;
    }
    @include max (767px){
        padding-bottom: 40px;
    }
    &.max-680{
        max-width: 680px;
    }
    &.max-750{
        max-width: 750px;
    }
   
    &.center{
        max-width: 650px;
        margin: 0 auto;
        text-align: center;
        &.max-width-700{
            max-width: 700px;
        }
        &.max-width-750{
            max-width: 750px;
        }
        &.max-width-850{
            max-width: 850px;
        }
    }
    
}
// default content
.uxora-default-content{
    h2{
        padding-bottom: 20px;
    }
    h3{
        padding-bottom: 20px;
    }
}
.title{
    padding-bottom: 20px;
}
.mt-50{
    margin-top: 50px;
    @include max (991px){
        margin-top: 40px;
    }
    @include max (767px){
        margin-top: 30px;
    }
}
.section{
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.pr-50{
    padding-right: 50px;
    @include max (991px){
        padding-right: 0;
    }
}
.pl-50{
    padding-left: 50px;
    @include max (991px){
        padding-left: 50px;
    }
}