
.uxora-social{
    &-wrap{
        ul{
            li{
                display: inline-block;
                margin-left: 10px;
                &:first-child{
                    margin-left: 0;
                }
                a{
                    width: 35px;
                    height: 35px;
                    display: flex;
                    align-items: center;
                    border-radius: 100px;
                    justify-content: center;
                    transition: all 0.4s;
                    border: 1px solid var(--heading-color);
                    &:hover{
                        border: 1px solid transparent;
                        background-color: var(--accent-bg);
                        path{
                            fill: var(--white-bg);
                        }
                    }
                    &.social2{
                        display: block;
                        border: none;
                        span{
                            position: relative;
                            &::before{
                              content: " ";
                              left: 0;
                              bottom: -3px;
                              width: 0;
                              height: 1px;
                              position: absolute;
                              transition: all .4s;
                              background: var(--heading-color);
                            }
                            &:hover::before{
                              width: 100%;
                            }
                          }
                        &:hover{
                            background-color: transparent;
                        }
                    }
                }
            }
        }
    }
    &-wrap2{
        width: 100%;
        height: 88px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: linear-gradient(135deg, #FCD569 0%, #0A23F4 100%);
        ul{
            li{
                display: inline-block;
                margin-right: 14.8px;
                transition: all 0.4s;
                &:last-child {
                    margin-right: 0;
                }
                a{
                    width: 40px;
                    height: 40px;
                    display: flex;
                    align-items: center;
                    border-radius: 100px;
                    justify-content: center;
                    transition: all 0.4s;
                    background-color: var(--light-color2);
                    border: 1px solid transparent;
                    &:hover{
                        border: 1px solid transparent;
                        background-color: var(--accent-color);
                        path{
                            fill: var(--white-bg);
                        }
                    }
                }
            }
        }
    }
    &-wrap3{
        display: flex;
        align-items: center;
        gap: 24px;
        ul{
            li{
                display: inline-block;
                margin-right: 14.8px;
                transition: all 0.4s;
                &:last-child {
                    margin-right: 0;
                }
                a{
                    width: 40px;
                    height: 40px;
                    display: flex;
                    align-items: center;
                    border-radius: 100px;
                    justify-content: center;
                    transition: all 0.4s;
                    background-color: var(--light-color2);
                    border: 1px solid transparent;
                    &:hover{
                        border: 1px solid transparent;
                        background-color: var(--accent-color);
                        path{
                            fill: var(--white-bg);
                        }
                    }
                }
            }
        }
    }
}
