.uxora-hero{
  &-section{
    padding: 190px 0 0;
    position: relative;
    z-index: 0;
    @include max (1199px){
      padding: 190px 0 0;
    }
    @include max (991px){
      padding: 190px 0 0;
    }
    @include max (767px){
      padding: 130px 0 0;
    }
  }
  &-rating{
    padding-bottom: 20px;
    ul{
      li{
        display: inline-block;
        img{
          padding: 0 10px 8px 0;
        }
      }
    }
  }
  &-content{
    position: relative;
    max-width: 1050px;
    margin: 0 auto;
    text-align: center;
    padding-bottom: 70px;
    p{
      font-size: 21px;
      line-height: 30px;
    }
    @include max (1199px){
      max-width: 800px;
    }
    @include max (991px){
      padding-bottom: 60px;
    }
    @include max (767px){
      padding-bottom: 40px;
    }
  }
  &-dashboard{
    position: relative;
    .overlay{
      position: absolute;
      bottom: 0;
      left: 0;
    }
  }
  // end one
  
}
