.uxora-footer{
  &-section{
    padding: 130px 0 0;
    @include max (991px){
      padding: 80px 0 0;
    }
    @include max (767px){
      padding: 60px 0 0;
    }
  }
  &-menu{
    padding-left: 60px;
    @include max (1399px){
      padding-left: 20px;
    }
    @include max (767px){
      padding-bottom: 30px;
    }
    h4{
      margin-bottom: 24px;
      position: relative;
      &::before{
        content: " ";
        left: -20px;
        top: 12px;
        width: 10px;
        height: 10px;
        position: absolute;
        transition: all .4s;
        border-radius: 100px;
        background: var(--accent-bg);
        @include max (1399px){
          left: -20px;
        }
      }
    }
    ul{
      li{
        margin-bottom: 10px;
        margin-left: -20px;
        &:last-child{
          margin-bottom: 0;
        }
        a{
          position: relative;
          transition: all 0.4s;
          font-size: 21px;
          line-height: 30px;
          color: var(--heading-color);
          &::before{
            content: " ";
            left: 0;
            bottom: -3px;
            width: 0;
            height: 1px;
            position: absolute;
            transition: all .4s;
            background: var(--heading-color);
          }
          &:hover::before{
            width: 100%;
          }
        }
      }
    }
    &.menu2{
      padding-left: 150px;
      @include max (1399px){
        padding-left: 210px;
      }
      @include max (1199px){
        padding-left: 20px;
        padding-top: 40px;
      }
      @include max (991px){
        padding-top: 0;
      }
      &::before{
        left: 125px;
        @include max (1399px){
          left: 185px;
        }
        @include max (1199px){
          left: 0;
          top: 50px;
        }
        @include max (991px){
          top: 10px;
        }
      }
    }
    &.menu3{
      @include max (1399px){
        padding-left: 150px;
        &::before{
          left: 125px;
        }
      }
      @include max (1199px){
        padding-left: 20px;
        padding-top: 40px;
        @include max (991px){
          padding-top: 0;
        }
        &::before{
          left: 0;
          top: 50px;
          @include max (991px){
            top: 10px;
          }
        }
      }
    }
  }
  &-textarea{
    @include max (1399px){
      margin-bottom: 30px;
      max-width: 500px;
    }
    img{
      padding-bottom: 24px;
    }
    p{
      margin-bottom: 24px;
      font-size: 21px;
      line-height: 30px;
    }
  }
  &-bottom-wrap{
    margin-top: 100px;
    border-top: 1px solid rgba(0, 6, 40, .1019607843);
    padding: 35px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include max (767px){
      display: block;
      text-align: center;
    }
    @include max (991px){
      margin-top: 80px;
    }
    @include max (767px){
      margin-top: 10px;
    }
    p{
      @include max (767px){
        margin-bottom: 15px;
      }
    }
    
  }
}
.pl-30{
  padding-left: 30px;
  @include max (1399px){
    padding-left: 50px;
  }
  @include max (1199px){
    padding-left: 0;
    padding-top: 40px;
  }
}
.pl-40{
  padding-left: 40px;
  @include max (1399px){
    padding-left: 70px;
  }
  @include max (1199px){
    padding-left: 0;
    padding-top: 40px;
  }
  @include max (767px){
    padding-top: 30px;
  }
}
.pl-100{
  padding-left: 100px;
  @include max (1399px){
    padding-left: 0;
  }
  @include max (1199px){
    padding-left: 100px;
  }
  @include max (767px){
    padding-left: 0;
  }
}
