input[type="text"],
input[type="email"],
input[type="password"],
input[type="url"],
input[type="tel"],
input[type="time"],
input[type="date"],
input[type="search"],
input, textarea {
  margin: 0;
  width: 100%;
  height: 60px;
  color:var(--heading-color);
  border-radius: 0;
  background-color: transparent;
  padding: 15px 0 ;
  border: none;
  transition: all 0.4s;
}
textarea {
  height: 145px;
  resize: none;
  &::placeholder{
   color: #181B0E99!important;
   }
  
}
.uxora-main{
   &-field{
      input{
         width: 300px;
         padding: 18px 24px;
         border-radius: 10px;
         font-size: 16px;
         line-height: 24px;
         border: 1px solid #00062880;
         border-radius: 3px;
         background-color: var(--white-bg);
         &::placeholder{
            color: #00062880;
         }
         @include max (767px){
            width: 100%;
         }
      }
      &-textarea{
         background: var(--light-color2);
         padding: 24px;
         border-radius: 10px;
         margin-bottom: 24px;
         p{
            margin-bottom: 10px;
            font-size: 18px;
            font-weight: 700;
            line-height: 30px;
         }
         &.textarea2{
            background-color: var(--light-color);
         }
      }
      &.field2{
         margin-bottom: 24px;
         p{
            margin-bottom: 10px;
            font-weight: 700;
         }
         input{
            width: 100%;
            border-radius: 3px;
            border: 1px solid transparent;
            background-color: var(--light-color2);
         }
      }
      &.field3{
         margin-bottom: 24px;
         p{
            margin-bottom: 10px;
         }
         input{
            width: 100%;
            padding: 18px 50px 18px 18px;
            border: 1px solid var(--light-color);
         }
      }
   }
}
.ml-15{
   margin-left: 15px;
   @include max (767px){
      margin-left: 0;
      margin-top: 24px;
   }
}
.field-icon {
   float: right;
   margin-right: 25px;
   margin-top: -38px;
   position: relative;
   z-index: 2;
   cursor: pointer;
}
.forgot-password {
   display: flex;
   flex-wrap: wrap;
   justify-content: space-between;
   a{
      font-size: 16px;
      line-height: 24px;
      color: var(--heading-color);
      @include max (767px){
         margin-top: 10px;
      }
   }
   label{
      span{
         color: var(--accent-color);
      }
   }
}
.form-bottom-text{
   margin-top: 10px;
   text-align: center;
   p{
      line-height: 24px;
      font-size: 16px;
      font-weight: 400;
      a{
         font-weight: 600;
         color: var(--heading-color);
      }
   }
}

.uxora-form-box{
   margin-bottom: 50px;
   @include max (991px){
      margin-bottom: 40px;
   }
   @include max (767px){
      margin-bottom: 30px;
   }
   input{
       padding: 0;
       height: initial;
       width: initial;
       margin-bottom: 0;
       display: none;
       cursor: pointer;
   }
   label{
      position: relative;
      cursor: pointer;
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      @include max(767px){
         display: block;
      }
      &::before{
         content: "";
         background-color: transparent;
         border: 2px solid #00062899;
         padding: 7px;
         display: inline-block;
         position: relative;
         vertical-align: middle;
         cursor: pointer;
         margin-right: 12px;
         margin-top: -2px;
      } 
   }
}
.uxora-form-box input:checked+label:after {
   content: "";
   display: block;
   position: absolute;
   top: 7px;
   left: 6px;
   width: 5px;
   height: 9px;
   border: 1px solid var(--heading-color);
   border-width: 0 2px 2px 0;
   transform: rotate(45deg);
}
