.uxora-contact{
    &-box{
        margin-top: 26px;
        padding: 30px;
        border-radius: 20px;
        border-radius: 10px;
        background-color: var(--light-color);
        h4{
            padding-bottom: 30px;
        }
        p{
            font-weight: 700;
        }
        &.box2{
            max-width: 630px;
            margin: 0 auto;
        }
        &.box4{
            max-width: 750px;
            margin: 0 auto;
        }
    }
    &-box3{
        padding: 30px;
        border-radius: 10px;
        background-color: var(--light-color2);
    }
}