.uxora-cta{
    &-content{
        max-width: 765px;
        @include max (1199px){
            max-width: 600px;
        }
        @include max (991px){
            max-width: 480px;
        }
        @include max (767px){
            max-width: 330px;
        }
        h2, p{
            color: var(--light-color);
        }
    }
    &-btn{
        padding-top: 50px;
        display: flex;
        gap: 24px;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        @include max (991px){
            padding-top: 40px;
        }
        @include max (767px){
            padding-top: 30px;
        }
    }
    // end
}

