.uxora-brand{
    &-section{
        padding-top: 100px;
        @include max (991px){
            padding-top: 60px;
        }
        @include max (767px){
            padding-top: 30px;
        }
    }
    &-title{
        @include max (991px){
            padding-bottom: 60px;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
        h5{
            font-size: 24px;
            line-height: 32px;
        }
    }
}
.uxora-brand-slider-init{
    .uxora-brand-slider-item{
        padding: 0 15px;
        margin-top: 15px;
        white-space: nowrap;
        @include max (1399px){
            margin-top: 30px;
        }
        @include max (1199px){
            margin-top: 45px;
        }
        @include max (991px){
            margin-top: 0;
        }
    }
}
.uxora_screenfix_right {
    margin-right: calc((-100vw + 100%) / 2);
    max-width: 1770px;
    
}