.uxora-faq{
  &-item{
    padding-bottom: 30px;
    border-bottom: 1px solid #0006281A;
    margin-bottom: 30px;
    &:last-child{
      padding-bottom: 0;
      border-bottom: none;
      margin-bottom: 0;
    }
    cursor: pointer;
    &.open {
      .uxora-faq-body{
        display: block;
      }
    }
  }
  &-header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 15px;
  }
  &-body{
    display: none;
    p{
      color: var(--light-color5);
    }
  }
}

.border-b{
  padding-bottom: 0;
  border-bottom: none;
  margin-bottom: 0;
}

.uxora-active-icon{
  flex-shrink: 0;
}

.open .uxora-active-icon img {
  transform: rotate(45deg);
}

.uxora-active-icon img {
  transition: all .4s;
}


/* Start Nav Styles */
.scroll-btn {
  position: sticky;
  z-index: 999;
  top: 200px;
  transition: all 0.4s;
  @include max (991px){
    margin-bottom: 40px;
  }
  @include max (767px){
    margin-bottom: 30px;
  }
  ul{
    li{
        margin-bottom: 15px;
        &:last-child{
          margin-bottom: 0;
        }
        a{
          &.questions{
            color: var(--heading-color);
            font-family: $heading-font;
            font-size: 24px;
            font-weight: 600;
            line-height: 32px;
          }
          &.active{
            color: var(--accent-color)!important;
            text-decoration: underline;
        }
    }
  }
}
}

/* End Nav Styles */



