.uxora-list-icon{
    &-content{
        ul{
            li{

                margin-bottom: 15px;
                position: relative;
                padding-left: 40px;
                color: var(--heading-color);
                @include max (575px){
                    font-size: 18px;
                }
                &:last-child{
                    margin-bottom: 0;
                }
                img{
                    position: absolute;
                    top: 4px;
                    left: 0;
                }
            }
        }
        &.content2{
            ul{
                li{
                    position: relative;
                    padding-left: 24px;
                    &::before{
                        top: 10px;
                        left: 0;
                        width: 8px;
                        height: 8px;
                        content: "";
                        position: absolute;
                        border-radius: 100px;
                        background-color: var(--heading-color);
                    }
                }
            }
        }
    }
}
