.uxora-pricing{
    &-wrap{
        padding: 30px;
        border-radius: 10px;
        transition: all .4s!important;
        margin-bottom: 24px;
        position: relative;
        border: 1px solid var(--light-color2);
        &:hover{
            border: 1px solid transparent;
            box-shadow: 0px 4px 50px 0px #0006281A;
        }
        &.active{
            border: 1px solid transparent;
            box-shadow: 0px 4px 50px 0px #0006281A;
        }
        @include max (991px){
            padding: 30px 20px;
        }
        &.wrap2{
            border: 1px solid var(--light-color);
            background-color: var(--light-color);
        }
    }
    &-header-top{
        font-size: 16px;
        font-weight: 700;
        line-height: 24px;
        background-color: var(--accent-bg);
        top: 0;
        left: 0;
        height: 50px;
        display: flex;
        align-items: center;
        border-radius: 10px 10px 0 0;
        justify-content: center;
        margin: -30px -30px 24px -30px;
        @include max (991px){
            margin: -30px -20px 24px -20px;
        }
        a{
            color: var(--light-color) !important;
        }
    }
    &-header{
        padding-bottom: 24px;
        &.header2{
            padding-bottom: 15px;
        }
    }
    &-price{
        gap: 5px;
        display: flex;
        align-items: flex-end;
        padding-bottom: 24px;
        &.price3{
            justify-content: center;
            padding-bottom: 12px;
        }
    }
    &-body{
        padding-bottom: 24px;
        border-bottom: 1px solid #0006281A;
        margin-bottom: 24px;
        ul{
            li{
                position: relative;
                padding-bottom: 15px;
                padding-left: 35px;
                font-size: 21px;
                font-weight: 600;
                line-height: 30px;
                &:last-child{
                    padding-bottom: 0;
                }
                img{
                    position: absolute;
                    left: 0;
                    top: 3px;
                }
            }
        }
    }
    &-data{
        p{
            font-weight: 700;
            margin-bottom: 24px;
        }
        ul{
            li{
                position: relative;
                padding-bottom: 8px;
                padding-left: 30px;
                &:last-child{
                    padding-bottom: 0;
                }
                img{
                    position: absolute;
                    left: 0;
                    top: 3px;
                }
            }
        }
    }
    &-footer{
      margin-top: 30px;
      &.footer2{
        margin: 0;
      }
    }
    // end 
}

.ux-price-value.dynamic-value {
    font-family: $heading-font;
    font-size: 80px;
    font-weight: 700;
    line-height: 1.125em;
    letter-spacing: -1px;
}

.toggle-btn{
    @include max (767px){
        padding: 10px 0 10px 10px;
    }
}

.toggle-switch {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}
.toggle-switch input {
    display: none;
}

.toggle-switch input:checked + span + span,
.toggle-switch input + span {
    padding: 13px 30px;
    background-color: var(--body-color);
}
.toggle-switch input + span + span,
.toggle-switch input:checked + span {
    background-color: transparent;
    border-color: transparent;
    padding: 16px 28px;
    color: var(--heading-color);
  }
// end switcher btn

// add table
    

.mt-40{
    margin-top: 40px;
}

.pricing-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 50px;
    @include max (991px){
        margin-bottom: 40px;
    }
    @include max (767px){
        margin-bottom: 30px;
        display: block;
    }
    label {
        margin: 0 20px;
        font-size: 18px;
        line-height: 30px;
        color: var(--heading-color);
        font-weight: 700;
        @include max(767px) {
        margin: 0 10px;
        font-size: 16px;
        }
        span{
            font-weight: 600;
        }
    }
}

[data-pricing-dynamic][data-value-active="yearly"] .dynamic-value:before {
  display: inline-block;
  content: attr(data-yearly);
  }
  [data-pricing-dynamic][data-value-active="yearly"] [data-pricing-trigger] {
  background: #ff5722;
  }
  
  [data-pricing-dynamic][data-value-active="yearly"] [data-pricing-trigger] span {
  left: calc(100% - 33px);
  }
  [data-pricing-dynamic][data-value-active="monthly"] .dynamic-value:before {
  display: inline-block;
  content: attr(data-monthly);
  }
  .dynamic-value:before {
    display: inline-block;
    content: attr(data-active);
  }
  .static-value:before {
    display: inline-block;
    content: attr(data-active);
  }
  .pricing-btn .btn-toggle {
    position: relative;
    width: 70px !important;
    height: 35px;
    border-radius: 50px;
    border: none;
    margin: 0;
    padding: 0!important;
    cursor: pointer;
    background-size: 25px;
    border: none!important;
    background-position: center right 5px;
    border: none !important;
    background-position: center right 5px;
    background-color: var(--accent-color);
    background-image: url(../images/pricing/switcher-shape.svg) !important;
}
 
.form-check-input:focus {
    border-color: none;
    outline: 0;
    box-shadow:none;
}
.pricing-btn .btn-toggle:checked {
    background-position: center left 5px;
    background-image: url(../images/pricing/switcher-shape.svg)!important;
}


// add table


tr { 
    text-align: center;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    @include max (1280px){
        padding: 20px;
    }
    span{
        font-weight: 400;
    }
}
tbody{
    margin-top: 50px;
}

.uxora-pricing-table {
    
    @include max(1349px) {
        overflow-x: auto;
    }
}
.sub-title{
    font-family: $heading-font;
    font-size: 24px;
    font-weight: 600;
    line-height: 32px;
    color: var(--heading-color);
}
.table-header{
    margin-bottom: 50px;
    @include max (991px){
        margin-bottom: 0;
    }
    .title{
        font-family: $heading-font;
        font-size: 80px;
        font-weight: 600;
        line-height: 1.125em;
        letter-spacing: -1px;
        color: var(--heading-color);
        @include max (1199px){
            font-size: 48px;
        }
        @include max (767px){
            font-size: 36px;
        }
    }
}
.table-sub-title{
    border-radius: 3px;
    background-color: var(--light-color2);
    &.title2{
        background-color: var(--light-color);
    }
}
th{
    padding: 24px 0 24px 24px;
    text-align: center;
    @include max (1260px){
        padding: 20px;
    }
    p{
        font-weight: 700;
        text-align: left;
        font-size: 15px;
    }
}
tr{
    @include max (991px){
        padding: 0!important;
    }
}


