.uxora-iconbox{
  &-wrap{
    padding: 30px;
    border-radius: 10px;
    transition: all .4s;
    position: relative;
    background-color: var(--light-color2);
    margin-bottom: 24px;
    &:hover{
      background-color: var(--accent-bg2);
    }
    &.wrap2{
      background-color: var(--light-color);
    }
  }
  &-icon{
      padding-bottom: 24px;
      
  }
  &-content-wrap{
      display: flex;
      align-items: center;
      justify-content: space-between;
      transition: all 0.4s;
      
  }
  &-btn{
      cursor: pointer;
      white-space: nowrap;
      overflow: hidden;
      --arrow-hover-move-x: -110%;
      position: relative;
  }
  // end
  &-wrap2{
    padding: 30px;
    border-radius: 10px;
    white-space: wrap;
    background-color: var(--light-color2);
  }
  &-rating{
    padding-bottom: 24px;
  }
  &-title2{
    padding-bottom: 24px;
    h4{
      padding-bottom: 24px;
    }
    p{
      font-size: 21px;
      line-height: 30px;
    }
  }
  &-author-wrap{
    display: flex;
    align-items: center;
    gap: 24px;
  }
  &-author-thumb{
    img{
      border-radius: 100px;
    }
  }
  &-author-title{
    h6{
      font-size: 21px;
      line-height: 30px;
      padding-bottom: 5px;
    }
  }
  &-content{
    h4{
      padding-bottom: 12px;
    }
  }
}
.uxora-t-slider-init .slick-slide {
  margin: 0 10px;
}

.uxora-iconbox-btn svg {
  transition: all 0.3s ease-out 0s;
}
.uxora-iconbox-btn:hover svg path:nth-of-type(1), .uxora-iconbox-btn:focus-visible svg path:nth-of-type(1) {
  transform: translateX(calc(-1* var(--arrow-hover-move-x)));
  opacity: 0;
  transition-delay: 0s, 0s;
}
.uxora-iconbox-btn svg path:nth-of-type(1) {
  transform: translateX(0);
  opacity: 1;
  transition-delay: 0.15s, 0.15s;
}
.uxora-iconbox-btn svg path {
  transition: transform 0.38s cubic-bezier(0.37, 0.08, 0.02, 0.93), opacity 0.18s ease-out;
}
.uxora-iconbox-btn:hover svg path:nth-of-type(2), .uxora-iconbox-btn:focus-visible svg path:nth-of-type(2) {
  transform: translateX(0);
  opacity: 1;
  transition-delay: 0.15s, 0.15s;
}
.uxora-iconbox-btn svg path:nth-of-type(2) {
  transform: translateX(calc(1* var(--arrow-hover-move-x)));
  opacity: 0.5;
  transition-delay: 0s, 0s;
}
.uxora-iconbox-btn svg path:nth-of-type(1) {
  transform: translateX(0);
  opacity: 1;
  transition-delay: 0.15s, 0.15s;
}
.uxora-iconbox-btn svg path:nth-of-type(2) {
  transform: translateX(calc(1* var(--arrow-hover-move-x)));
  opacity: 0.5;
  transition-delay: 0s, 0s;
}