// fonts variable
$body-font: "Inter", serif!default;
$heading-font: "Outfit", serif!default;

// color variable
:root {
  --heading-color: #000628;
  --accent-color: #5523E8;
  --accent-bg: #5523E8;
  --accent-bg2: #BBAFFE;
  --white-color: #ffffff;
  --white-bg: #ffffff;
  --light-color: #FAF8F6;
  --light-color2: #EDE7E2;

}

$colors: (
 
);

$bodyfontScale: (
  xl: (
    20: 20px,
    lineheight: 32px,
  ),
  lg: (
    18: 18px,
    lineheight: 28px,
  ),
  md: (
    16: 16px,
    lineheight: 24px,
  ),
  xs: (
    14: 14px,
    lineheight: 22px,
  ),
  xss: (
    12: 12px,
    lineheight: 20px,
  ),
);

// button font size
$button_font_size: 16px;
$buton_line_height: 1.5em;
$buton_border_radius: 3px;
$button_padding: 17px 32px;
$button_font_weight: 700;
$button_font_family: $body-font;